import { memo } from 'react';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { InputNumber } from 'primereact/inputnumber';
import Select from 'react-select';

function Modal(props) {
  const { purchaseline, actionDelete, item, handleChangeItem, onChange, handleAddPurchaseLine, handeModalApproval } = props;  
  
  const options = item?.map(item => ({
    value: `${item.Code} - ${item.Variant} - ${item.Unit} - ${item.Price} - ${item.Name}`,
    label: `${item.Code} - ${item.Name}`
  })) || [];
  
  const quantityEditor = (options) => {
    return <InputNumber value={options.value} maxLength = "7" min = "0" style={styles.font} onValueChange={(e) => options.editorCallback(e.value)} />;
  };

  const renderActionButtons = () => {
    if (purchaseline.length === 0) return null;
    switch (purchaseline[0].Status) {
      case '6':
        return (<button type="button" className="btn btn-outline-success" style = {styles.text} data-bs-dismiss="modal" onClick = {handeModalApproval}>Gi Duyt </button>);
      default:
        return null;
    }
  };
  
  const footer = () => {
    const total = purchaseline ? 
                  purchaseline.reduce((sum, item) => sum + Number(item.Amount), 0) : 0;
    return (
      <div style={styles.totals}>  
       <span>Tng gi tr: {new Intl.NumberFormat('en-US').format(total)}</span>
      </div>
    );
  };
  return (
    <div className="modal fade" id="detail" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" style={styles.text} id="staticBackdropLabel">Chi tit</h5>
                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <div className="modal-body">
                  <div className="row g-3">
                    <div className="col-md-6 col-sm-6">
                        <label className="form-label" style = {styles.text}>Chn sn phm</label>
                        <Select options = {options}
                                onChange = {handleChangeItem}
                                placeholder = "Chn sn phm"
                                styles={{ container: base => ({ ...base, ...styles.text }) }}/>
                    </div>
                    <div className="col-md-6 col-sm-6">
                        <label className="form-label" style = {styles.text} >S lng</label>
                        <input className="form-control" type='number' name="quantity" style={styles.font} maxLength = "7" min = "1" onChange={(event)=>onChange(event)} required/>
                    </div>
                    <div className="col-md-12 col-sm-12">
                      <button style={styles.font} className="btn btn-outline-info" onClick={handleAddPurchaseLine}>Thm</button>
                    </div>
                  </div>
                  <br/>
                  <DataTable value={purchaseline} paginator showGridlines rows={5} footer={footer}
                             emptyMessage="Khng c d liu" style={styles.font}>
                    <Column field="ID" header="ID" hidden></Column>
                    <Column field="ItemCode" header="M hng"  style={{ width: '10%' }}></Column>
                    <Column field="Variant" header="M mu" style={{ width: '5%' }}></Column>
                    <Column field="ItemName" header="Tn hng" style={{ width: '50%' }}></Column>
                    <Column field="Unit" header="vt" style={{ width: '5%' }}></Column>
                    <Column field="QuantityOld" header="S lng c" style={{ width: '5%' }}></Column>
                    <Column field="Quantity" header="S lng" style={{ width: '10%' }} editor={(options) => quantityEditor(options)}></Column>
                    <Column field="Amount" header="Gi tr" style={{ width: '5%' }}></Column>
                    <Column field="Status" header="Thao tc" body={actionDelete} style={{ width: '10%' }}></Column>
                  </DataTable> 
              </div>
              <div className="modal-footer">
                { renderActionButtons() }
                <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal" style={styles.text}>Thot</button>
              </div>
            </div>
          </div>
    </div>
  )
}

export default memo(Modal);

const styles = {
  text : {
    fontFamily:"Times New Roman",
    fontWeight: 500
  },
  font: {fontFamily:"Times New Roman"},
  totals : {
    fontFamily:"Times New Roman",
    color: "red",
    fontSize: '1em',
  },
}
