import { memo, useState, useEffect, useRef } from 'react';
import { Panel } from 'primereact/panel';
import { Toast } from 'primereact/toast';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { InputText } from 'primereact/inputtext';
import Form from './form';
import {formDate_toDate} from '../../../hooks';
import { postApiv1 } from '../../../api';
import Spinner from '../../../components/Spinner';

function Statistical() {
  const [loading, setLoading] = useState(false);
  const [reports, setReports] = useState([]);
  const [inputs, setInputs] = useState({});
  const [globalFilter, setGlobalFilter] = useState(null);
  
  const dt = useRef(null);
  const toast = useRef(null);

  useEffect(() => {
    setInputs(formDate_toDate);
  }, []);
  
  const onChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs(values => ({...values, [name]: value}));
  }

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      setLoading(true);
      let formData = new FormData(); 
      formData.append('fromdate', inputs.formDate);
      formData.append('todate', inputs.toDate);
      const result = await postApiv1("statistical", formData);
      setReports(result.data)
      setLoading(false);
    } catch (error) {
      return toast.current.show({severity:"error", summary: "Error", detail:"Li khi ti d liu", life: 3000});
    }
  }
  
  const exportCSV = () => { dt.current.exportCSV() };

  const header = () => {
    return (<div className="flex justify-content-between">
              <span className="p-input-icon-left">
                <i className="pi pi-search" />
                <InputText style={styles.InputText} type="search" onInput={(e) => setGlobalFilter(e.target.value)} placeholder="Search..." />
              </span>
            </div>);
  };

  return ( 
    <div className="row">
      <Panel style={styles.header} header="Bo co thng k yu cu hng ha">
        <Form inputs = {inputs} onChange = {onChange}
              handleSubmit = {handleSubmit} exportCSV = {exportCSV}/>
        <br/>
        <DataTable ref={dt} value={reports} 
                   paginator showGridlines rows={25} 
                   globalFilter={globalFilter} header={header}
                   emptyMessage="Khng c d liu" style={styles.dataTable}>
          <Column field="ItemCode" header="M" style={{ width: '5%' }}></Column>
          <Column field="Variant" header="M mu" style={{ width: '5%' }}></Column>
          <Column field="ItemName" header="Tn sn phm" style={{ width: '20%' }}></Column>
          <Column field="Unit" header="vt (ERP)" style={{ width: '5%' }}></Column>
          <Column field="Quantity" header="S lng" style={{ width: '5%' }}></Column>
          <Column field="PriceCost" header="Gi vn (VAT)" style={{ width: '5%' }}> </Column>
          <Column field="PriceLevel1" header="Gi bn l (VAT)" style={{ width: '5%' }}> </Column>
          <Column field="TotalInventory" header="Tng tn kho" style={{ width: '5%' }}> </Column>
          <Column field="InventoryNhapKhau" header="Tn kho NK" style={{ width: '5%' }}> </Column>
          <Column field="InventoryBan" header="Tn kho bn" style={{ width: '5%' }}> </Column>
          <Column field="SlMua" header="S lng mua (2 nm)" style={{ width: '5%' }}> </Column>
          <Column field="SlBan" header="S lng bn (2 nm)" style={{ width: '5%' }}></Column>
          <Column field="SlTra" header="S lng tr (2 nm)" style={{ width: '5%' }}></Column>
          <Column field="LKBan" header="Ly k bn hng(nm nay)" style={{ width: '5%' }}></Column>
        </DataTable>
      </Panel>
      {loading && <Spinner />}
      <Toast ref = {toast}/>
    </div>
  );
}

export default memo(Statistical);
const styles = {
  header: {fontFamily:"Times New Roman"},
  InputText: {fontFamily:"Times New Roman"},
  dataTable: {fontFamily:"Times New Roman"},
  title:{
    textAlign:"center",
    color:"red",
    padding:"10px",
    fontFamily:"Times New Roman"
  },
  margin_top:{
    marginTop:"20px",
    fontFamily:"Times New Roman",
    marginBottom:"20px",
  },
}
 